function BackdropHandler(props: any){
  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  }
  
  return (
    <div>
    <Button onClick={handleToggle} style={{color: 'black'}}>{props.buttonValue}</Button>
    <Backdrop
      sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
      open={open}
      onClick={handleClose}
    >
      {props.content}
    </Backdrop>
  </div>
  )
}

function ImageCarousel() {
  let [currentIndex, setCurrentIndexState] = React.useState(0);
  let images = [image1, image6, image2, image7, image5, image3];

  React.useEffect(() => {
    const interval = setInterval(() => {
      let newIndex = currentIndex === (images.length-1) ? 0 : currentIndex+1;
      console.log(newIndex);
      setCurrentIndexState(newIndex);
    }, 1000);
    return () => clearInterval(interval);
  }, [currentIndex]);

  return (
    <img className="carousel-image" src={images[currentIndex]} alt="" />
  )
}

